.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Estilos generales de la aplicación */
.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(to right, #ffe6e6, #ffc6c6);
  color: #5a5a5a;
  font-size: calc(1rem + 2vmin); /* Ajuste de tamaño de fuente relativo */
}

/* Estilos para el contenedor de las imágenes en las esquinas */
.corner-image {
  position: absolute;
  width: 15vw; /* Ancho relativo para dispositivos móviles */
  height: 15vw; /* Altura relativa para dispositivos móviles */
  max-width: 100px; /* Máximo ancho para asegurar tamaño mínimo */
  max-height: 100px; /* Máximo alto para asegurar tamaño mínimo */
  border-radius: 50%; /* Hace que las imágenes sean circulares */
  overflow: hidden; /* Asegura que la imagen no se salga del borde redondeado */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Sombra suave */
   object-fit: cover; /* Ajusta la imagen para cubrir el contenedor, recortando si es necesario */
}

.corner-image img {
  width: 100%; /* Ancho de la imagen al 100% del contenedor */
  height: 100%; /* Altura de la imagen al 100% del contenedor */
  object-fit: cover; /* Ajusta la imagen para cubrir el contenedor, recortando si es necesario */
}

/* Estilos específicos para cada esquina */
.top-left {
  top: 3vh; /* Ajuste relativo para dispositivos móviles */
  left: 3vh; /* Ajuste relativo para dispositivos móviles */
  height: 12vh; /* Altura relativa para dispositivos móviles */
}

.top-right {
  top: 3vh; /* Ajuste relativo para dispositivos móviles */
  right: 3vh; /* Ajuste relativo para dispositivos móviles */
}

.bottom-left {
  bottom: 3vh; /* Ajuste relativo para dispositivos móviles */
  left: 3vh; /* Ajuste relativo para dispositivos móviles */
  height: 12vh; /* Altura relativa para dispositivos móviles */
}

.bottom-right {
  bottom: 3vh; /* Ajuste relativo para dispositivos móviles */
  right: 3vh; /* Ajuste relativo para dispositivos móviles */
}

/* Estilos para el contenido principal */
.header-wrapper {
  margin-bottom: 2rem;
  position: relative; /* Agregado para alinear correctamente las esquinas */
}

.header-content {
  margin-bottom: 2rem;
  position: relative; /* Agregado para alinear correctamente las esquinas */
}

.header-wrapper h1::before,
.header-wrapper h1::after {
  content: "🩵👑";
  font-size: 1.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.header-wrapper h1::before {
  left: -4rem; /* Ajuste relativo para dispositivos móviles */
}

.header-wrapper h1::after {
  right: -4rem; /* Ajuste relativo para dispositivos móviles */
}

.header-wrapper h2 {
  font-size: 1.5rem;
  color: #e67a7a;
  margin: 0;
}

.is-christmas-wrapper,
.countdown-wrapper {
  background: #fce4ec;
  border: 2px solid #f48fb1;
  border-radius: 1rem; /* Borde redondeado relativo */
  padding: 2rem; /* Espaciado interno relativo */
  margin-bottom: 2rem; /* Margen inferior relativo */
}

.countdown-wrapper span {
  font-size: 2.5rem;
  color: #c2185b;
  font-weight: bold;
}

button {
  background: #f48fb1;
  border: none;
  border-radius: 2rem; /* Borde redondeado relativo */
  padding: 1.5rem 3rem; /* Ajuste de padding relativo */
  font-size: 1.2rem;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease;
}

button:hover {
  background: #e91e63;
}

/* Estilos adicionales para decoración romántica */
.App {
  background: linear-gradient(to right, #ffe6e6, #ffc6c6);
  color: #5a5a5a;
}

.header-wrapper h1 {
  font-size: 3rem;
  color: #c21848;
  margin-bottom: 1rem;
  position: relative;
  text-align: center;
}

.header-wrapper h2 {
  font-size: 1.8rem;
  color: #f48fb1;
  margin: 0;
  text-align: center;
}

/* Media query para dispositivos móviles */
@media (max-width: 768px) {
  .corner-image {
      width: 15vw; /* Ajuste de tamaño relativo para dispositivos móviles */
      height: 15vw; /* Ajuste de tamaño relativo para dispositivos móviles */
      max-width: 100px; /* Máximo ancho para asegurar tamaño mínimo */
      max-height: 100px; /* Máximo alto para asegurar tamaño mínimo */
  }

  .header-wrapper h1::before,
  .header-wrapper h1::after {
      display: none; /* Oculta los emojis en pantallas más pequeñas */
  }
}

